import {connect} from "react-redux";
import React, {Component} from "react";
import {
    updateComparatorDevicesList,
    fetchComparatorDevicesList,
    fetchProducers,
    redirectToComparator,
    storeIsComparatorCategory
} from "eshop/modules/simfree/actions/comparator";
import {getComparatorDevices, getSelectedCategory, getIsComparatorCategory} from "eshop/modules/simfree/selectors";
import {getFiltersUrl} from "eshop/modules/configurator/selectors/filters";
import ComparatorAddDeviceModal from "eshop/modules/simfree/components/comparator/ComparatorAddDeviceModal";
import ComparatorErrorModal from "eshop/modules/simfree/components/comparator/ComparatorErrorModal";
import {getMarketContextPrefixUrl} from "eshop/modules/configurator/selectors/metaData";

class ComparatorChooserComponent extends Component {
    constructor(props) {
        super(props);
    }
    componentWillMount() {
        this.props.fetchComparatorDevicesList();
    }
    componentDidMount() {
        this.checkIsComparatorCategory(this.props);
        this.loadModules();
    }
    componentDidUpdate() {
        this.loadModules();
    }
    componentWillReceiveProps(nextProps, nextContext) {
        if (this.props.selectedCategory !== nextProps.selectedCategory) {
            this.checkIsComparatorCategory(nextProps);
        }
    }

    loadModules() {
        if (document.getElementById("opl-comparator-chooser-component-content") != null) {
            OPL.UI.initModules(document.getElementById("opl-comparator-chooser-component-content"));
        }
    }

    checkIsComparatorCategory(props) {
        let isComparatorCategory = props.categories && props.categories.filter(category => category === props.selectedCategory).length > 0;
        this.props.storeIsComparatorCategory(isComparatorCategory);
    }

    getComparatorUrl(){

        return ((this.props.marketContextPrefixUrl)+this.props.comparatorPageUrl) + '?' + this.props.filtersUrl + '&selectedCategory=' + this.props.selectedCategory;
    }


    render() {
        return (
            <div id="opl-comparator-chooser-component">
                {this.props.isComparatorCategory && this.props.comparatorDevices && this.props.comparatorDevices.length > 0 &&
                <div id="opl-comparator-chooser-component-content" className="l-row u-margin-top-l-xl">
                        <div className="l-col l-col-12">
                            <h2 className="h3">{this.props.title}</h2>
                            <div className="opl-compare-products">
                                <ComparatorAddDeviceModal title={this.props.title}
                                                          trigger={"opl-compare-products__modal-trigger"}/>
                                {this.props.comparatorDevices && this.props.comparatorDevices.map((device, index) => {
                                        let className;
                                        if (index <= 1) {
                                            className = "opl-compare-products__box u-margin-right-m u-small-no-margin-r";
                                        } else {
                                            className = "opl-compare-products__box u-large-margin-right-xxl";
                                        }
                                        return <section key={device.productId}
                                                        className={className}>
                                            <div id={"box-item-" + device.productId} className="opl-compare-products__item g-gray1-bg"
                                                 data-js-module='[{"path":"common/modules/opl-ellipsis","conditions": "element:{seen}", "options":{"maxLength": "40", "smallMaxLength" : "15"}}]'>
                                                <span className="opl-compare-products__item-btn g-icon g-icon--close" role="button"
                                                      onClick={() => this.props.updateComparatorDevices(device)}>
                                                    <span className="u-acc-hide">Usu≈Ñ</span>
                                                </span>
                                                <img className="opl-compare-products__item-img u-margin-right"
                                                     src={device.imageUrl} alt=""/>
                                                <p className="opl-compare-products__item-name ellipsis-item u-font-bold">{device.name}</p>
                                            </div>
                                        </section>
                                    }
                                )}
                                {[...Array(3 - this.props.comparatorDevices.length)].map((x, i) => {
                                        let className;
                                        if (this.props.comparatorDevices.length === 2 || (this.props.comparatorDevices.length === 1 && i === 1)) {
                                            className = "opl-compare-products__box u-large-margin-right-xxl";
                                        } else {
                                            className = "opl-compare-products__box u-margin-right-m u-small-no-margin-r";
                                        }
                                        return <section key={"empty" + i} className={className}>
                                            <a id="opl-compare-products__modal-trigger"
                                               className="opl-btn--box o-btn o-modal__trigger"
                                               href="#opl-compare-products__modal"
                                               data-js-module="core/modules/modal"
                                               onClick={() => this.props.fetchProducers()}>
                                                <span
                                                    className="u-spacing-right g-icon--xs-s g-icon g-icon--plus"/>Dodaj<br/>urzƒÖdzenie
                                            </a>
                                        </section>
                                    }
                                )}
                                <button disabled={this.props.comparatorDevices.length < 2}
                                        onClick={() => this.props.redirectToComparator(this.getComparatorUrl())}
                                        className="opl-compare-products__btn opl-btn opl-btn--primary o-btn u-small-margin-top-l u-medium-margin-top-l">
                                    <span>Por√≥wnaj</span>
                                </button>
                            </div>
                        </div>
                </div>
                }
                <ComparatorErrorModal modalCategoryErrorMessage={this.props.modalCategoryErrorMessage}
                                      modalDevicesLimitErrorMessage={this.props.modalDevicesLimitErrorMessage}
                                      modalLeftButtonText={this.props.modalLeftButtonText}
                                      modalRightButtonText={this.props.modalRightButtonText}
                                      comparatorPageUrl={this.getComparatorUrl()}/>
            </div>

        )
    }
}

const mapStateToProps = state => ({
    comparatorDevices: getComparatorDevices(state),
    selectedCategory: getSelectedCategory(state),
    isComparatorCategory: getIsComparatorCategory(state),
    filtersUrl: getFiltersUrl(state),
    marketContextPrefixUrl:getMarketContextPrefixUrl(state),
});

const mapDispatchToProps = (dispatch, props) => ({
    redirectToComparator: (url) => dispatch(redirectToComparator(url)),
    fetchComparatorDevicesList: () => dispatch(fetchComparatorDevicesList()),
    updateComparatorDevices: (device) => dispatch(updateComparatorDevicesList(device)),
    storeIsComparatorCategory: (isComparatorCategory) => dispatch(storeIsComparatorCategory(isComparatorCategory)),
    fetchProducers: () => dispatch(fetchProducers())
});

export default connect(mapStateToProps, mapDispatchToProps)(ComparatorChooserComponent);
